.nav {
  position: fixed;
  z-index: 2;

  padding: 20px 0;

  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  min-height: $navHeight;

  border-bottom: 1px solid $separateColor;
  background-color: #242424;

  &__inner {
    padding: 0 15px;

    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__buttons {
    position: absolute;
    display: flex;
    align-items: center;
    left: 6em;
  }

  &__language-button {
    border: none;
    background-color: transparent;
    color: #FFFFFF;
    cursor: pointer;
    font-size: 1.5em;
    outline: none;

    &-active {
      text-decoration: underline;
    }
  }
}

.logo {
  padding-left: 10px;

  display: inline-block;

  &__text {
    @extend %amita-700;
    font-size: 48px;
    line-height: 1;

    text-transform: uppercase;
    color: #FFFFFF;
  }
}